
import XHeader from 'vux/src/components/x-header/index.vue'

import { types } from "util";
export default {
name: "FileLogin",
components: {
XHeader
},
data() {
return {
PWD: "",
PWD1: "",
PWD2: "",
PWDtest: true,
PWD1test: true,
PWD2test: true,
disable: true
};
},
watch: {
PWD(val) {
this.changetext();
},
PWD1(val) {
this.changetext();
},
PWD2(val) {
this.changetext();
}
},
methods: {
testpwd(str) {
var regStr = /[0-9]/;
var complex = 0;
if (regStr.test(str)) {
++complex;
}
if (complex < 1 || str.length != 6) {
return false;
} else {
return true;
}
},
changetext() {
this.PWDtest = this.testpwd(this.PWD) ? true : false;
this.PWD1test = this.testpwd(this.PWD1) ? true : false;
this.PWD2test = this.PWD2 != this.PWD1 && this.PWD1 != "" ? false : true;
if (
this.PWD2test &&
this.PWD1test &&
this.PWD.length == 6 &&
this.PWD1.length == 6 &&
this.PWD2.length == 6
) {
this.disable = false;
} else {
this.disable = true;
}
},
NextStep() {
if (this.disable) {
return false;
}
this.$ajax
.post("", {
"MSH.1": "healthFileAssociateService",
"MSH.2": "updatePassword",
token: this.$root.loginInfo.token,
oldmm: this.PWD,
newmm: this.PWD1
})
.then(data => {
if (typeof data != "undefined") {
if (data.status == 1) {
let that = this;
this.$vux.toast.show({
text: "修改成功",
type: "success",
onHide() {
that.$router.push("/FileLogin");
}
});
} else if (data.status == 2) {
this.PWDtest = false;
this.disable = true;
}
}
})
.catch(error => {
console.log(error);
});
}
}
};



